import React, { Component } from 'react'

import Layout from "../components/layout"
import SEO from "../components/seo"
import WorkNav from '../components/workNav'
import CreateTiles from '../components/createTiles'
import Cookies from 'universal-cookie'

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/work") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }
      entries(section: [projects], hidden: false) {
        ... on Craft_Projects {
          hidden
          slug
          title
          previewTile {
            url
            width
            height
            ... on Craft_UploadsVolume {
              url

              imageFile {
                childImageSharp {
                  fluid(maxWidth: 650) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                  }
                }
              }

              optimizedimages {
                colorPalette
              }
            }
          }
          description {
            content
          }
          categories {
            title
          }
          disciplines {
            title
          }
        }
      }
    }
  }    
`;

